.About img {
  max-width: 25vw;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}
.header_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 800px) {
  .About img {
    max-width: 70vw;
  }
  .About {
    margin-top: 5rem;
  }
}
