.calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

.mathplayground h1 {
  border-bottom: 1px solid #fff;
  padding-bottom: 0.2rem;
}

.calculator input[type="number"] {
  padding: 0.2rem 0.5rem;
  border: none;
  outline: none;
  margin: 1rem 0;
}

.calculator select {
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  border: none;
  outline: none;
}

/* age calc */

.age_calculator {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#date-picker {
  padding: 0.5rem;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  letter-spacing: 0.2rem;
}

.age_results {
  margin: 2rem 0;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem;
  visibility: hidden;
}
.age_results div {
  text-align: center;
  padding: 0.5rem ;
}

.age_results div:nth-child(even) {
  background: rgba(39, 221, 253, 0.589);
}
.age_results div:nth-child(odd) {
  background: rgba(46, 255, 185, 0.589);
}

@media (max-width: 768px) {
  .age_results {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}
