.header {
  text-align: center;
}

.header h2,
.header h1 {
  text-shadow: 0 0 0.2rem red, 0 0 0.4rem red, 0 0 0.6rem red, 0 0 0.8rem red,
    0 0 1rem red, 0 0 1.2rem red;
}

.header h2 {
  font-size: 2.5rem;
}

.header h1 {
  font-size: 4rem;
}

/* mrsaad animation */
#logo {
  animation: fill 1s ease forwards 4s, zoom 4s ease forwards,
    opacity 2s ease forwards;
  overflow: visible;
}

@keyframes fill {
  0% {
    fill: transparent;
  }

  100% {
    fill: black;
  }
}

@keyframes zoom {
  from {
    transform: scale(2);
  }
  to {
    transform: scale(0.5);
  }
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#logo path:nth-child(1) {
  stroke-dasharray: 2044;
  stroke-dashoffset: 2044;
  animation: line 3s ease-in-out forwards;
}
#logo path:nth-child(2) {
  stroke-dasharray: 1989;
  stroke-dashoffset: 1989;
  animation: line 3s ease-in-out forwards 0.3s;
}
#logo path:nth-child(3) {
  stroke-dasharray: 1438;
  stroke-dashoffset: 1438;
  animation: line 3s ease-in-out forwards 0.6s;
}
#logo path:nth-child(4) {
  stroke-dasharray: 1677;
  stroke-dashoffset: 1677;
  animation: line 3s ease-in-out forwards 0.9s;
}
#logo path:nth-child(5) {
  stroke-dasharray: 1677;
  stroke-dashoffset: 1677;
  animation: line 3s ease-in-out forwards 1.2s;
}
#logo path:nth-child(6) {
  stroke-dasharray: 1975;
  stroke-dashoffset: 1975;
  animation: line 3s ease-in-out forwards 1.5s;
}

@keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 768px) {
  .header h2 {
    font-size: 2rem;
  }
  .header h1 {
    font-size: 1.7rem;
  }
  #logo {
    width: 35rem;
  }
}
