@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
  overflow: hidden;
}

body::before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -29;
  background-image: linear-gradient(45deg, #333333, #dd1818);
  background-size: cover;
  animation: bg 7s ease-in-out infinite;
}

@keyframes bg {
  0%,
  100% {
    background-position: left;
    filter: hue-rotate(45deg);
  }
  50% {
    background-position: right;
    filter: hue-rotate(360deg);
  }
}

.border-b {
  border-bottom: 2px solid #555;
}
.navigation_bar {
  z-index: 100;
  position: fixed;
  transition: all 0.5s ease;
}
.sticky {
  background: #111111;
}
.main_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inputs {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #fff !important;
  color: #fff;
}
.inputs,
.inputs:focus,
.inputs::placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}

.privacy a,
.terms a {
  color: #fff;
}
.privacy,
.terms {
  margin-top: 3rem;
}
.privacy p,
.terms p {
  margin: 2.5rem 0;
}
