.main_projects_container {
  margin: 4rem 0;
}

.projects {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.project {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.img_container {
  width: 100%;
  position: relative;
}
.img_container .btnView {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img_container a {
  transform: translateY(70%);
  color: #000;
  padding: 0.5rem 1rem;
  border: 1px solid #000;
  border-radius: 1rem;
  background: #fff;
  font-size: 2rem;
  opacity: 0;
  transition: all 0.2s ease;
}
.img_container:hover a {
  transform: translateY(0);
  opacity: 1;
}
.img_container:hover .btnView {
  opacity: 1;
}

.img_container img {
  max-width: 100%;
  max-height: 20rem;
}

.project .details {
  display: flex;
  flex-direction: column;
}

.project a,
.project h3 {
  margin: 0.5rem 0;
  text-align: center;
  display: block;
}
.project h3 {
  font-size: 1rem;
}

@media (max-width: 768px) {
  .projects {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-gap: 4rem;
  }
}
