@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
.Socials {
  display: flex;
  font-size: 2.5rem;
}

.Socials a {
  height: 30%;
  color: #fff;
  margin: 0 0.5rem;
  text-shadow: 0 0 1rem #000;
  display: block;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.Socials a:hover {
  text-shadow: 0 0.7rem 0.5rem #000;
  transform: translateY(-5px);
  -webkit-transform: translateY(-5px);
  -moz-transform: translateY(-5px);
  -ms-transform: translateY(-5px);
  -o-transform: translateY(-5px);
}

@font-face {
  font-family: "aristotelica";
  src: url(/static/media/aristotelica.display-trial-regular.42c60a5f.ttf);
}

.saad {
  font-family: aristotelica;
  font-size: 4rem;
  letter-spacing: 0.5rem;
}

.services {
  padding-left: 0;
  list-style: none;
}

.services a {
  text-decoration: none;
  color: #fff;
  line-height: 2rem;
  position: relative;
  padding-bottom: 0.3rem;
}

.services a::after {
  position: absolute;
  content: "";
  background: white;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.services a:hover::after {
  width: 100%;
}

.end_text {
  background: #111;
}
.end_text p {
  margin: 0;
  padding: 0;
  line-height: 5rem;
}

@media (max-width: 768px) {
  .Socials {
    font-size: 1.7rem;
  }
  .saad {
    font-size: 2.5rem;
  }
}

.header {
  text-align: center;
}

.header h2,
.header h1 {
  text-shadow: 0 0 0.2rem red, 0 0 0.4rem red, 0 0 0.6rem red, 0 0 0.8rem red,
    0 0 1rem red, 0 0 1.2rem red;
}

.header h2 {
  font-size: 2.5rem;
}

.header h1 {
  font-size: 4rem;
}

/* mrsaad animation */
#logo {
  -webkit-animation: fill 1s ease forwards 4s, zoom 4s ease forwards,
    opacity 2s ease forwards;
          animation: fill 1s ease forwards 4s, zoom 4s ease forwards,
    opacity 2s ease forwards;
  overflow: visible;
}

@-webkit-keyframes fill {
  0% {
    fill: transparent;
  }

  100% {
    fill: black;
  }
}

@keyframes fill {
  0% {
    fill: transparent;
  }

  100% {
    fill: black;
  }
}

@-webkit-keyframes zoom {
  from {
    transform: scale(2);
  }
  to {
    transform: scale(0.5);
  }
}

@keyframes zoom {
  from {
    transform: scale(2);
  }
  to {
    transform: scale(0.5);
  }
}

@-webkit-keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#logo path:nth-child(1) {
  stroke-dasharray: 2044;
  stroke-dashoffset: 2044;
  -webkit-animation: line 3s ease-in-out forwards;
          animation: line 3s ease-in-out forwards;
}
#logo path:nth-child(2) {
  stroke-dasharray: 1989;
  stroke-dashoffset: 1989;
  -webkit-animation: line 3s ease-in-out forwards 0.3s;
          animation: line 3s ease-in-out forwards 0.3s;
}
#logo path:nth-child(3) {
  stroke-dasharray: 1438;
  stroke-dashoffset: 1438;
  -webkit-animation: line 3s ease-in-out forwards 0.6s;
          animation: line 3s ease-in-out forwards 0.6s;
}
#logo path:nth-child(4) {
  stroke-dasharray: 1677;
  stroke-dashoffset: 1677;
  -webkit-animation: line 3s ease-in-out forwards 0.9s;
          animation: line 3s ease-in-out forwards 0.9s;
}
#logo path:nth-child(5) {
  stroke-dasharray: 1677;
  stroke-dashoffset: 1677;
  -webkit-animation: line 3s ease-in-out forwards 1.2s;
          animation: line 3s ease-in-out forwards 1.2s;
}
#logo path:nth-child(6) {
  stroke-dasharray: 1975;
  stroke-dashoffset: 1975;
  -webkit-animation: line 3s ease-in-out forwards 1.5s;
          animation: line 3s ease-in-out forwards 1.5s;
}

@-webkit-keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 768px) {
  .header h2 {
    font-size: 2rem;
  }
  .header h1 {
    font-size: 1.7rem;
  }
  #logo {
    width: 35rem;
  }
}

.About img {
  max-width: 25vw;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}
.header_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 800px) {
  .About img {
    max-width: 70vw;
  }
  .About {
    margin-top: 5rem;
  }
}

.Contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 65vh;
  padding: 1rem 2rem 1rem 2rem;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.Contact form {
  width: 70%;
}

textarea {
  resize: none;
}

@media (max-width: 786px) {
  .Contact form {
    width: 100%;
  }
}

tbody td:nth-child(5):hover {
  color: red;
}
.covid_container {
  max-height: 80vh;
  overflow: auto;
}

.covid_container table {
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .covid_container {
    font-size: 75%;
  }
  .covid-heading {
    font-size: 1.2rem;
  }
}

.main_projects_container {
  margin: 4rem 0;
}

.projects {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.project {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.img_container {
  width: 100%;
  position: relative;
}
.img_container .btnView {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img_container a {
  transform: translateY(70%);
  color: #000;
  padding: 0.5rem 1rem;
  border: 1px solid #000;
  border-radius: 1rem;
  background: #fff;
  font-size: 2rem;
  opacity: 0;
  transition: all 0.2s ease;
}
.img_container:hover a {
  transform: translateY(0);
  opacity: 1;
}
.img_container:hover .btnView {
  opacity: 1;
}

.img_container img {
  max-width: 100%;
  max-height: 20rem;
}

.project .details {
  display: flex;
  flex-direction: column;
}

.project a,
.project h3 {
  margin: 0.5rem 0;
  text-align: center;
  display: block;
}
.project h3 {
  font-size: 1rem;
}

@media (max-width: 768px) {
  .projects {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-gap: 4rem;
  }
}

:root {
  --main_bg: #1e272e;
  --main_clr: #0097e6;
}
.temp_form {
  width: 100%;
  position: relative;
  margin: 4rem auto;
}
.temp_form .city_field {
  width: 100%;
  padding: 2rem;
  background: #1e202b;
  color: white;
  outline: none;
  border: none;
  font-size: 1.7rem;
  border-radius: 3rem;
}
.temp_form .city_field::-webkit-input-placeholder {
  color: #bfc1c8;
}
.temp_form .city_field:-ms-input-placeholder {
  color: #bfc1c8;
}
.temp_form .city_field::-ms-input-placeholder {
  color: #bfc1c8;
}
.temp_form .city_field::placeholder {
  color: #bfc1c8;
}
.temp_form #submitBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  bottom: 5px;
  border: none;
  outline: none;
  background: #0097e6;
  background: var(--main_clr);
  padding: 0 3rem;
  border-radius: 3rem;
  color: white;
  font-size: 1.7rem;
  transition: all 0.2s ease-in-out;
}
.temp_form #submitBtn:hover {
  color: #0097e6;
  color: var(--main_clr);
  background-color: #fff;
}
.tempInformation {
  width: 40rem;
  margin: auto;
  background-color: #1e272e;
  background-color: var(--main_bg);
  border-radius: 0.5rem;
  padding: 1rem;
}
.top_layer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #fff;
}
.top_layer p {
  color: #fff;
  font-size: 1.5rem;
  padding: 0;
  margin: 0;
}
.main_layer {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* padding: 1rem; */
}
.main_layer #city_name {
  text-align: center;
  margin: 2rem 0;
  font-size: 3rem;
}
.main_layer .middle_layer {
  display: flex;
  justify-content: space-around;
}
.main_layer .middle_layer p,
.main_layer .middle_layer .fa {
  font-size: 5rem;
}

@media (max-width: 768px) {
  .main_layer .middle_layer p,
  .main_layer .middle_layer .fa {
    font-size: 2.5rem;
  }
  .temp_form .city_field {
    padding: 1rem;
    font-size: 1rem;
  }
  .temp_form #submitBtn {
    padding: 1rem;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

.mathplayground h1 {
  border-bottom: 1px solid #fff;
  padding-bottom: 0.2rem;
}

.calculator input[type="number"] {
  padding: 0.2rem 0.5rem;
  border: none;
  outline: none;
  margin: 1rem 0;
}

.calculator select {
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  border: none;
  outline: none;
}

/* age calc */

.age_calculator {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#date-picker {
  padding: 0.5rem;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  letter-spacing: 0.2rem;
}

.age_results {
  margin: 2rem 0;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem;
  visibility: hidden;
}
.age_results div {
  text-align: center;
  padding: 0.5rem ;
}

.age_results div:nth-child(even) {
  background: rgba(39, 221, 253, 0.589);
}
.age_results div:nth-child(odd) {
  background: rgba(46, 255, 185, 0.589);
}

@media (max-width: 768px) {
  .age_results {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
  overflow: hidden;
}

body::before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -29;
  background-image: linear-gradient(45deg, #333333, #dd1818);
  background-size: cover;
  -webkit-animation: bg 7s ease-in-out infinite;
          animation: bg 7s ease-in-out infinite;
}

@-webkit-keyframes bg {
  0%,
  100% {
    background-position: left;
    -webkit-filter: hue-rotate(45deg);
            filter: hue-rotate(45deg);
  }
  50% {
    background-position: right;
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

@keyframes bg {
  0%,
  100% {
    background-position: left;
    -webkit-filter: hue-rotate(45deg);
            filter: hue-rotate(45deg);
  }
  50% {
    background-position: right;
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

.border-b {
  border-bottom: 2px solid #555;
}
.navigation_bar {
  z-index: 100;
  position: fixed;
  transition: all 0.5s ease;
}
.sticky {
  background: #111111;
}
.main_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inputs {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #fff !important;
  color: #fff;
}
.inputs::-webkit-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs:-ms-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs::-ms-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs,
.inputs:focus,
.inputs::placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}

.privacy a,
.terms a {
  color: #fff;
}
.privacy,
.terms {
  margin-top: 3rem;
}
.privacy p,
.terms p {
  margin: 2.5rem 0;
}

