:root {
  --main_bg: #1e272e;
  --main_clr: #0097e6;
}
.temp_form {
  width: 100%;
  position: relative;
  margin: 4rem auto;
}
.temp_form .city_field {
  width: 100%;
  padding: 2rem;
  background: #1e202b;
  color: white;
  outline: none;
  border: none;
  font-size: 1.7rem;
  border-radius: 3rem;
}
.temp_form .city_field::placeholder {
  color: #bfc1c8;
}
.temp_form #submitBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  bottom: 5px;
  border: none;
  outline: none;
  background: var(--main_clr);
  padding: 0 3rem;
  border-radius: 3rem;
  color: white;
  font-size: 1.7rem;
  transition: all 0.2s ease-in-out;
}
.temp_form #submitBtn:hover {
  color: var(--main_clr);
  background-color: #fff;
}
.tempInformation {
  width: 40rem;
  margin: auto;
  background-color: var(--main_bg);
  border-radius: 0.5rem;
  padding: 1rem;
}
.top_layer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #fff;
}
.top_layer p {
  color: #fff;
  font-size: 1.5rem;
  padding: 0;
  margin: 0;
}
.main_layer {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* padding: 1rem; */
}
.main_layer #city_name {
  text-align: center;
  margin: 2rem 0;
  font-size: 3rem;
}
.main_layer .middle_layer {
  display: flex;
  justify-content: space-around;
}
.main_layer .middle_layer p,
.main_layer .middle_layer .fa {
  font-size: 5rem;
}

@media (max-width: 768px) {
  .main_layer .middle_layer p,
  .main_layer .middle_layer .fa {
    font-size: 2.5rem;
  }
  .temp_form .city_field {
    padding: 1rem;
    font-size: 1rem;
  }
  .temp_form #submitBtn {
    padding: 1rem;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
